import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { MenuController } from 'ionic-angular';
import { ModalController } from 'ionic-angular';
/**
 * Generated class for the InicioPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-inicio',
  templateUrl: 'inicio.html',
})





export class InicioPage {
inicioSesion=false;
  constructor(public menuCtrl: MenuController,public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController, public events: Events,public modalCtrl: ModalController) {


  this.categorias = [];


  events.subscribe('loginOK', () => {
    // user and time are the same arguments passed in `events.publish(user, time)`
          this.apiProvider.verificarLogin()
    .then(data => { 

      console.log(data);

      if(data){

      this.inicioSesion=false;
      }
      else{
      this.inicioSesion=true;
      } 
      
      
     });


     
  });



  }

  ionViewDidLoad() {

    console.log('ionViewDidLoad InicioPage');

      this.apiProvider.categoriasHome()
      .then(data => {
       console.log(data);
       if(data){
        this.categorias = data || [];
       }
       else{console.log('Ha ocurrido un error');}
       
      });

  }

    ionViewDidEnter() {
    
    

        this.apiProvider.verificarLogin()
    .then(data => { 

      console.log(data);

      if(data){

      this.inicioSesion=false;
      }
      else{
      this.inicioSesion=true;
      } 
      
      
     });


  }



    goServicio(nombreServicio){
console.log(nombreServicio);
  this.navCtrl.push('ListaServiciosPage', {'nombre':nombreServicio});

  }


    goMapa(){
//console.log(nombreServicio);
  //this.navCtrl.setRoot('MapaPage');
this.navCtrl.push('MapaPage');
  }

      openMenu(){
   let profileModal = this.modalCtrl.create('LoginPage');
   profileModal.present();

  }

  


setDefaultPic(nombre) {
console.log(nombre);
  return  "assets/imgs/"+nombre;
}





}



